@charset "UTF-8";
/* ===== Глобальные стили ===== */
/* Normalize */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: "ProximaNova", Arial, sans-serif !important;
  color: #000;
  background: #ECECEC;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  font-family: inherit;
  cursor: pointer;
  border: none;
  background: none;
}

figure {
  margin-bottom: 0 !important;
}

/* Контейнер */
.container {
  max-width: 1360px;
  margin: 0 auto;
}

/* Скрытие */
.hidden {
  display: none !important;
}

/* Кнопки */
.button {
  display: inline-block;
  padding: 12px 20px;
  font-size: 16px;
  border-radius: 10px;
  text-align: center;
  text-decoration: none;
  transition: background 0.3s;
}

.black-button {
  background: #000;
  color: #fff;
}

.white-button {
  background: #fff;
  color: #000;
  border: 1px solid #000;
}

/* Breadcrumbs */
.breadcrumbs {
  font-size: 14px;
  margin: 15px 0 30px 0;
}

.breadcrumbs a {
  color: #000;
}

/* Общий стиль для блоков */
section {
  background-color: #eee;
}

/* Глобальный стиль для прокрутки */
* {
  scrollbar-width: thin;
  scrollbar-color: #ccc transparent;
}

/* WebKit (Chrome, Safari, Edge) */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 4px;
  transition: background-color 0.3s;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #999;
}

html, body {
  height: 100%;
  margin: 0;
}

/* Стили для страницы 404 */
.error-page {
  padding: 120px 0;
  text-align: center;
}
.error-page .error-wrapper {
  max-width: 600px;
  margin: 0 auto;
}
.error-page .error-code {
  font-size: 100px;
  font-weight: 900;
  color: #ccc;
}
.error-page .error-title {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 20px;
}
.error-page .error-message {
  font-size: 18px;
  color: #666;
  margin-bottom: 30px;
}
.error-page .error-btn {
  background: #111;
  padding: 14px;
  color: #fff;
}
.error-page .error-btn:hover {
  background: #333;
}

/* Цвета */
:root {
  --c-bg:#ffffff;
  --c-text:#0b0b0d;
  --c-muted:#6b7280;
  --c-blue:#0b2bff; /* фирменный синий */
  --c-blue-2:#1b33ff; /* темнее для hover */
  --c-blue-soft:#eef2ff;
  --c-orange:#ff9d0a; /* кнопка CTA */
  --c-green:#2fb24a; /* “в наявності” */
  --c-card:#f4f6f8;
  --c-line:#e5e7eb;
}

/* Типографика и размеры */
/* Брейкпоинты */
.site-header {
  background: #f8f8f8;
  padding: 14px 0;
}
.site-header .header-inner {
  max-width: min(1440px, 95%);
  margin: 0 auto;
  padding: 0 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
}
.site-header .header-left img {
  height: 46px;
  width: auto;
  display: block;
}
.site-header .header-menu .main-nav {
  display: flex;
  gap: 48px;
  list-style: none;
}
.site-header .header-menu .main-nav li a {
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  color: #111;
  transition: color 0.2s;
}
.site-header .header-menu .main-nav li a:hover {
  color: #0057ff;
}
.site-header .header-right .btn {
  background: #003dff;
  color: white;
  font-weight: 600;
  padding: 12px 22px;
  border-radius: 8px;
  display: inline-block;
  transition: background 0.2s;
}
.site-header .header-right .btn:hover {
  background: #002bcc;
}

.site-footer {
  background: #f8f8f8;
  border-top: 1px solid #e4e4e4;
  padding-top: 24px;
  padding-bottom: 16px;
}
.site-footer .footer-inner {
  max-width: min(1440px, 95%);
  margin: 0 auto;
  padding: 0 16px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 40px;
}
.site-footer .footer-col {
  flex: 1;
}
.site-footer .footer-col--brand {
  max-width: 260px;
}
.site-footer .footer-col--nav {
  display: flex;
  justify-content: center;
}
.site-footer .footer-col--contacts {
  max-width: 260px;
  text-align: right;
  font-size: 14px;
  line-height: 1.4;
}
.site-footer .custom-logo {
  max-height: 40px;
  width: auto;
  display: block;
  margin-bottom: 12px;
}
.site-footer .footer-copy {
  font-size: 14px;
  color: #666;
  margin: 0;
}
.site-footer .footer-menu {
  list-style: none;
  display: flex;
  gap: 32px;
  padding: 0;
  margin: 0;
}
.site-footer .footer-menu li a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: #111;
  transition: color 0.2s;
}
.site-footer .footer-menu li a:hover {
  color: #0057ff;
}
.site-footer .footer-phone {
  font-weight: 600;
  font-size: 15px;
}
.site-footer .footer-schedule {
  margin-top: 8px;
  font-size: 13px;
  color: #666;
}
.site-footer .footer-bottom {
  max-width: min(1440px, 95%);
  margin: 16px auto 0;
  padding: 0 16px;
  border-top: 1px solid #e4e4e4;
  padding-top: 10px;
}
.site-footer .footer-bottom .footer-legal {
  margin: 0;
  font-size: 12px;
  color: #9a9a9a;
}
@media (max-width: 900px) {
  .site-footer .footer-inner {
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
  }
  .site-footer .footer-col--contacts {
    text-align: left;
  }
  .site-footer .footer-menu {
    flex-wrap: wrap;
    justify-content: flex-start;
    row-gap: 8px;
  }
}

/* ============================================================================================== Product cards ===== */
.wa-product-cards .wp-block-columns {
  gap: clamp(16px, 3vw, 32px);
  margin-bottom: 0;
}
.wa-product-cards .wp-block-image img {
  border-radius: 12px;
}
.wa-product-cards .wp-block-button__link {
  border-radius: 10px;
  padding: 0.9rem 1.2rem;
}

/* ===================================================================================================== ZigZag ===== */
.wa-zigzag .zigzag-row {
  align-items: center !important;
  margin-bottom: 0;
  gap: 0;
}
.wa-zigzag .zigzag-row p {
  padding: clamp(16px, 3vw, 32px);
}
.wa-zigzag .wp-block-image img {
  border-radius: 12px;
}

/* ============================================================================================= Feature Panel ===== */
.wa-fp {
  display: grid;
  grid-template-columns: minmax(0, 1fr) clamp(260px, 28vw, 420px);
  gap: clamp(20px, 3vw, 40px);
}
.wa-fp-left > * + * {
  margin-top: clamp(10px, 1.5vw, 16px);
}
.wa-fp-right {
  background: #fff;
  border: 1px solid var(--c-line, #e6e6e6);
  border-radius: 16px;
  padding: clamp(16px, 2.4vw, 24px);
  position: relative;
}
.wa-fp-header {
  margin-bottom: clamp(16px, 2vw, 20px);
  border-bottom: 1px solid var(--c-line, #eee);
  padding-bottom: 12px;
}
.wa-fp-title {
  font-weight: 800;
  font-size: clamp(22px, 2.4vw, 28px);
  line-height: 1;
}
.wa-fp-subtitle {
  color: #666;
  margin-top: 4px;
  font-size: 0.95rem;
}
.wa-fp-row {
  margin-top: clamp(14px, 1.6vw, 18px);
}
.wa-fp-row-title {
  font-weight: 600;
  margin-bottom: 10px;
}
.wa-fp-options {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 10px;
}

@media (max-width: 900px) {
  .wa-fp {
    grid-template-columns: 1fr;
  }
  .wa-fp-options {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 600px) {
  .wa-fp-options {
    grid-template-columns: repeat(2, 1fr);
  }
}
.wa-opt {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 64px;
  border: 1px solid #e3e6ef;
  border-radius: 12px;
  background: #fff;
  font-weight: 600;
  cursor: default;
}
.wa-opt[aria-pressed=true], .wa-opt:hover {
  border-color: #3b82f6;
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.15);
}

/* ============================================================================================= HERO + SLIDER ===== */
.wa-slider .swiper {
  width: 100%;
  height: 100%;
}
.wa-slider img {
  width: 580px;
  height: 550px;
  display: block;
  border-radius: 12px;
  margin: 0 auto;
  object-fit: contain;
}
.wa-slider .swiper-button-prev,
.wa-slider .swiper-button-next {
  color: #000;
  top: calc(50% - 22px);
}
.wa-slider .swiper-pagination-bullet {
  background: #000;
}

.wa-section.wa-hero-fold {
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.wa-hero {
  width: 100%;
  align-items: center;
  column-gap: clamp(32px, 5vw, 64px);
}

.wa-hero-left {
  border-radius: 32px;
  overflow: hidden;
}
.wa-hero-left .wa-section__inner,
.wa-hero-left .wa-slider .wa-section__inner {
  padding: 0;
}

.wa-hero-right h2 {
  font-size: clamp(32px, 4vw, 44px);
  margin-bottom: 16px;
}
.wa-hero-right .wp-block-buttons {
  gap: 16px;
  margin-top: 32px;
}

/* =============================================================================================== CONTACT CTA ===== */
.wa-contact {
  padding-block: clamp(40px, 8vw, 96px);
  /* верхняя строка */
  /* нижняя строка */
  /* синяя карточка */
}
.wa-contact-bottom > .wp-block-column:last-child {
  flex-basis: calc(100% - 750px) !important;
}
.wa-contact-bottom > .wp-block-column:first-child {
  flex-basis: 700px !important;
}
.wa-contact-top {
  align-items: flex-start;
  margin-bottom: clamp(32px, 6vw, 56px);
}
.wa-contact-top h2 {
  font-size: clamp(40px, 4.5vw, 64px);
  line-height: 1.05;
  font-weight: 800;
  letter-spacing: -0.02em;
}
.wa-contact-top p {
  font-size: clamp(16px, 1.2vw, 18px);
}
.wa-contact-bottom {
  align-items: stretch;
  gap: clamp(32px, 4vw, 56px);
}
.wa-contact-card {
  background: url("/wp-content/uploads/2025/11/1-1.png");
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 32px;
  position: relative;
  overflow: hidden;
  color: #fff;
  min-height: 545px;
  display: flex;
  align-items: end;
  width: 700px;
  justify-content: center;
}
.wa-contact-card-inner {
  position: relative;
  z-index: 1;
}
.wa-contact-card input[type=tel] {
  width: 100%;
  border-radius: 12px;
  border: none;
  padding: 16px 18px;
  font-size: 16px;
  line-height: 1.4;
  outline: none;
}
.wa-contact-card input[type=submit] {
  width: 100%;
  border: none;
  border-radius: 12px;
  padding: 16px 18px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  background: #ff9800;
  color: #fff;
  transition: background 0.2s ease, transform 0.1s ease;
}
.wa-contact-card input[type=submit]:hover {
  background: #ffb027;
  transform: translateY(-1px);
}
.wa-contact-fields {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.wa-contact-fields p {
  display: flex;
  flex-direction: column;
}
.wa-contact-kicker {
  font-weight: 800;
  font-size: clamp(26px, 3vw, 36px);
  line-height: 1.1;
  text-transform: uppercase;
  margin: 0 0 12px;
}
.wa-contact-subtitle {
  font-size: clamp(16px, 1.3vw, 18px);
  margin: 0 0 24px;
}
.wa-contact-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 16px;
  font-size: 16px;
}
.wa-contact-logo {
  max-width: 180px;
}
.wa-contact-note {
  font-weight: 600;
}
.wa-contact-small {
  font-size: 12px;
  color: #777;
  line-height: 1.4;
}

.wpcf7-spinner {
  position: absolute;
  top: -15px;
  right: -50px;
}

@media (max-width: 900px) {
  .wa-contact-top, .wa-contact-bottom {
    flex-direction: column;
  }
  .wa-contact-card {
    border-radius: 24px;
    background: #0011ff;
  }
  .wa-contact-info {
    align-items: flex-start;
  }
  .wa-contact-fields p {
    margin: 0;
  }
}
@media (max-width: 600px) {
  .wa-contact-card {
    padding: 20px;
    border-radius: 20px;
  }
  .wa-contact-kicker {
    font-size: 22px;
  }
}
/* ========================================================================================= Scroll animations ===== */
/* --- Fade-in/out, выезд из-за края экрана --- */
.wa-anim-fade-up .wa-fade-item,
.wa-anim-fade-down .wa-fade-item,
.wa-anim-fade-left .wa-fade-item,
.wa-anim-fade-right .wa-fade-item {
  opacity: 0;
  will-change: transform, opacity;
  transition: transform 0.9s cubic-bezier(0.22, 0.61, 0.36, 1), opacity 0.9s ease-out;
}

.wa-anim-fade-up .wa-fade-up {
  transform: translate3d(0, 30vh, 0);
}

.wa-anim-fade-down .wa-fade-down {
  transform: translate3d(0, -30vh, 0);
}

.wa-anim-fade-left .wa-fade-left {
  transform: translate3d(-40vw, 0, 0);
}

.wa-anim-fade-right .wa-fade-right {
  transform: translate3d(40vw, 0, 0);
}

.wa-anim-fade-up.wa-fade-active .wa-fade-item,
.wa-anim-fade-down.wa-fade-active .wa-fade-item,
.wa-anim-fade-left.wa-fade-active .wa-fade-item,
.wa-anim-fade-right.wa-fade-active .wa-fade-item {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

/* --- Parallax --- */
.wa-parallax-layer {
  will-change: transform;
}

.wa-anim-parallax-soft .wa-parallax-layer {
  transition: transform 0.2s ease-out;
}

/* ================================================================= */
/* === Горизонтальные карточки (horizontal-cards) ================== */
/* ================================================================= */
.wa-hcards-wrapper {
  position: relative;
}
.wa-hcards-wrapper .wa-hcards-inner {
  position: sticky;
  top: 0;
  overflow: hidden;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track {
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
  gap: 50px;
  padding-inline: 25px;
  will-change: transform;
  transition: transform 0.25s ease-out;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item {
  flex: 0 0 auto;
  display: flex;
  align-items: baseline;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item > * {
  margin: 0 !important;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item .wp-block-columns {
  width: auto;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  gap: 0;
  max-width: 500px;
}
@media (max-width: 1024px) {
  .wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item .wp-block-columns {
    max-width: 320px;
    padding: 20px;
  }
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item .zigzag-row {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 675px;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item .zigzag-row.reverse {
  flex-direction: column-reverse;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item .wp-block-image {
  width: auto;
  max-width: 550px;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item img {
  height: auto;
  width: 100%;
  display: block;
  border-radius: 16px;
}

@media (max-width: 900px) {
  .wa-hcards-track {
    gap: 20px;
    padding-inline: 16px;
  }
}
/* ===== Benefits grid ===== */
.wa-benefits {
  padding-block: clamp(40px, 7vw, 80px);
}
.wa-benefits-grid > div {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-rows: 1fr;
  gap: clamp(16px, 2vw, 24px);
}

.wa-benefit {
  background: #f8f9ff;
  border-radius: 24px;
  padding: clamp(20px, 2.4vw, 28px);
  box-shadow: 0 24px 60px rgba(26, 44, 116, 0.06);
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.wa-benefit__icon {
  width: 64px;
  height: 64px;
  border-radius: 24px;
  background: #eef2ff;
  margin-bottom: 8px;
}
.wa-benefit__title {
  font-weight: 800;
  font-size: clamp(20px, 1.6vw, 22px);
  line-height: 1.2;
  margin: 0 0 8px;
}
.wa-benefit__text {
  margin: 0;
  color: #555;
  font-size: 0.98rem;
}
.wa-benefit--highlight {
  grid-row: 1/span 2;
  grid-column: 3;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: minmax(0, 1.1fr) minmax(0, 1fr);
  gap: 24px;
}
.wa-benefit--highlight .wa-benefit__icon,
.wa-benefit--highlight .wa-benefit__body {
  grid-column: 1;
}
.wa-benefit--highlight .wa-benefit__image {
  grid-row: 1/span 2;
  grid-column: 2;
  border-radius: 24px;
  overflow: hidden;
  background-size: cover;
  background-position: center;
}

.wa-benefits-cta {
  margin-top: clamp(28px, 4vw, 40px);
  justify-content: center;
}
.wa-benefits-cta .wp-block-button__link {
  padding: 16px 32px;
  border-radius: 999px;
  font-weight: 700;
}

@media (max-width: 750px) {
  /* Сетка — только один столбец */
  .wa-benefits-grid > .wp-block-group__inner-container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px; /* можешь менять */
  }
  /* Большая карточка — обычная */
  .wa-benefit--highlight {
    display: flex;
    flex-direction: column;
    grid-column: auto !important;
    grid-row: auto !important;
  }
  /* Скрываем картинку в большой карточке */
  .wa-benefit--highlight .wa-benefit__image {
    display: none !important;
  }
  /* Иконка + текст остаются нормальными */
  .wa-benefit {
    padding: 20px;
  }
  .wa-benefit__title {
    font-size: 20px;
  }
  .wa-benefit__text {
    font-size: 16px;
  }
}
