.wa-quiz-modal {
  position: fixed;
  inset: 0;
  z-index: 9999;
  display: none;
  align-items: center;
  justify-content: center;
  font-family: inherit;
}
.wa-quiz-modal.is-open {
  display: flex;
}
.wa-quiz-modal .wa-quiz-backdrop {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
}
.wa-quiz-modal .wa-quiz-dialog {
  position: relative;
  z-index: 1;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(15, 23, 42, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  /* =========================
     ШАГИ
  ========================= */
  /* =========================
     ОПЦИИ (radio/checkbox)
  ========================= */
  /* =========================
     ТЕКСТОВЫЙ INPUT
  ========================= */
  /* =========================
     ФУТЕР
  ========================= */
  /* =========================
     ФОРМА (ПОСЛЕ ПОСЛЕДНЕГО ШАГА)
  ========================= */
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-close {
  position: absolute;
  top: 12px;
  right: 16px;
  border: none;
  background: transparent;
  font-size: 24px;
  cursor: pointer;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-header {
  padding: 24px 28px 16px;
  border-bottom: 1px solid #e5e7eb;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-header .wa-quiz-title {
  font-size: 24px;
  font-weight: 800;
  margin: 0 0 6px;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-header .wa-quiz-subtitle {
  margin: 0;
  color: #6b7280;
  font-size: 14px;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-body {
  padding: 20px 28px;
  overflow-y: auto;
  flex: 1 1 auto;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-steps {
  position: relative;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-steps .wa-quiz-step {
  display: none;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-steps .wa-quiz-step.is-active {
  display: block;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-steps .wa-quiz-step .wa-quiz-step-title {
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 8px;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-steps .wa-quiz-step .wa-quiz-step-subtitle {
  margin: 0 0 16px;
  color: #6b7280;
  font-size: 14px;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-steps .wa-quiz-step .wa-quiz-step-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 12px;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-option {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 6px;
  padding: 10px 12px;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  cursor: pointer;
  transition: border-color 0.15s ease, box-shadow 0.15s ease, transform 0.1s ease;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-option input {
  display: none;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-option .wa-quiz-option-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-option .wa-quiz-option-label {
  font-size: 14px;
  font-weight: 600;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-option:hover {
  border-color: #3b82f6;
  box-shadow: 0 0 0 1px rgba(59, 130, 246, 0.25);
  transform: translateY(-1px);
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-input-text {
  width: 100%;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  padding: 10px 12px;
  font-size: 14px;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-footer {
  padding: 12px 28px 20px;
  border-top: 1px solid #e5e7eb;
  display: flex;
  justify-content: space-between;
  gap: 12px;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-footer .wa-quiz-prev,
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-footer .wa-quiz-next {
  border-radius: 999px;
  border: none;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-footer .wa-quiz-prev {
  background: #e5e7eb;
  color: #111827;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-footer .wa-quiz-next {
  background: #3b82f6;
  color: #fff;
}
.wa-quiz-modal .wa-quiz-dialog .wa-quiz-form-wrapper {
  display: none;
  padding: 20px 28px 28px;
  border-top: 1px solid #e5e7eb;
}

/* =========================
   АДАПТИВ
========================= */
@media (max-width: 640px) {
  .wa-quiz-modal .wa-quiz-dialog {
    max-width: 100%;
    margin: 0 10px;
    border-radius: 16px;
  }
  .wa-quiz-modal .wa-quiz-step-options {
    grid-template-columns: 1fr !important;
  }
}
/* =========================
   1) Reveal Children
========================= */
/* базовое состояние: скрыт */
.wa-anim-reveal-child {
  opacity: 0;
  transform: translate3d(0, 20px, 0);
  transition: opacity 0.5s ease-out var(--wa-anim-delay, 0ms), transform 0.6s ease-out var(--wa-anim-delay, 0ms);
}

/* когда элемент вошёл в вьюпорт */
.wa-anim-reveal-child.is-visible {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

/* =========================
   3) Horizontal Cards
========================= */
.wa-hcards-wrapper {
  position: relative;
}
.wa-hcards-wrapper .wa-hcards-inner {
  position: sticky;
  top: 0;
  overflow: hidden;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track {
  display: flex;
  height: 100%;
  will-change: transform;
}
.wa-hcards-wrapper .wa-hcards-inner .wa-hcards-track .wa-hcards-item {
  display: flex;
  align-items: baseline;
  justify-content: center;
  padding: 1.5rem;
  box-sizing: border-box;
}

@media (max-width: 1024px) {
  .wa-hcards-wrapper--slider .wa-hcards-track {
    touch-action: pan-y;
  }
  .wa-hcards-wrapper {
    position: relative;
    overflow: hidden;
  }
  .wa-hcards-inner {
    width: 100%;
    max-width: 100%;
  }
  .wa-hcards-track {
    display: flex;
    align-items: stretch;
    will-change: transform;
    touch-action: pan-y;
  }
  .wa-hcards-track p {
    font-size: 13px !important;
    padding: 0 !important;
  }
  .wa-hcards-item {
    box-sizing: border-box;
    padding: 0 12px !important;
    display: flex;
    align-items: stretch;
    max-width: 320px;
  }
  .wa-hcards-item .zigzag-row {
    min-height: 500px !important;
  }
  .wa-hcards-item > * {
    width: 100%;
    max-width: 320px;
    margin: 0 auto;
  }
  .wa-hcards-wrapper--stack {
    overflow: visible;
  }
  .wa-hcards-wrapper--stack .wa-hcards-inner,
  .wa-hcards-wrapper--stack .wa-hcards-track {
    overflow: visible;
  }
  .wa-hcards-wrapper--stack .wa-hcards-track {
    display: block;
  }
  .wa-hcards-wrapper--stack .wa-hcards-item {
    padding: 0;
  }
}
/* =========================
   Parallax
========================= */
[data-wa-anim=parallax-soft] {
  position: relative;
  overflow: hidden;
}

.wa-quiz-lock {
  overflow: hidden;
}

.wa-quiz-modal {
  position: fixed;
  inset: 0;
  display: none;
  z-index: 9999;
}

.wa-quiz-modal.is-open {
  display: flex;
  align-items: center;
  justify-content: center;
}

.wa-quiz-modal__overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
}

.wa-quiz-modal__dialog {
  position: relative;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  background: #fff;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.wa-quiz-modal__header {
  padding: 20px 24px 12px;
  border-bottom: 1px solid #eee;
}

.wa-quiz-modal__title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
}

.wa-quiz-modal__progress {
  height: 4px;
  background: #f0f0f0;
  border-radius: 999px;
  overflow: hidden;
}

.wa-quiz-modal__progress-bar {
  height: 100%;
  width: 0;
  background: #1a73e8;
  transition: width 0.25s ease;
}

.wa-quiz-modal__close {
  position: absolute;
  top: 12px;
  right: 16px;
  border: none;
  background: transparent;
  font-size: 22px;
  line-height: 1;
  cursor: pointer;
}

.wa-quiz {
  padding: 24px 24px 20px;
  overflow-y: auto;
}

.wa-quiz-step {
  display: none;
}

.wa-quiz-step.is-active {
  display: block;
}

.wa-quiz__footer {
  display: flex;
  justify-content: space-between;
  gap: 12px;
  padding: 12px 24px 20px;
  border-top: 1px solid #eee;
}

.wa-quiz-btn {
  border-radius: 999px;
  padding: 10px 22px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  background: #1a73e8;
  color: #fff;
}

.wa-quiz-btn--ghost {
  background: #f1f3f4;
  color: #202124;
}

html, body {
  background: var(--c-bg);
  color: var(--c-text);
  font-family: "Inter", ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, "Noto Sans", Arial, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 16px;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.alignwide {
  max-width: min(min(1440px, 95%) + 2 * 16px, 100% - 2rem);
  margin-inline: auto;
}

.alignfull {
  width: 100vw;
  margin-left: 50%;
  transform: translateX(-50%);
}

.wp-block-columns {
  column-gap: clamp(20px, 2.8vw, 40px);
}

.wp-block-image img {
  border-radius: 18px;
  box-shadow: none;
}

.wa-section {
  padding-block: clamp(24px, 4vw, 64px);
}

.wa-section__inner {
  max-width: min(1440px, 95%);
  margin: 0 auto;
}

.stock {
  color: var(--c-green);
  font-weight: 600;
}

hr {
  border: none;
  border-top: 1px solid var(--c-line);
}

/* Лёгкий baseline для секций с data-wa-anim */
.wa-section[data-wa-anim] {
  opacity: 1;
  transform: none;
  transition: opacity 0.5s ease, transform 0.6s ease;
}

.wa-anim-fade-in {
  opacity: 0;
  transform: translateY(8px);
}

.wa-anim-fade-up {
  opacity: 0;
  transform: translateY(18px);
}

.wa-anim-parallax {
  will-change: transform;
}

.wa-anim-sticky-scroll {
  position: relative;
}

.wa-anim-horizontal-snap {
  scroll-snap-type: x mandatory;
  overflow-x: auto;
}

/* Когда твой js добавит класс .is-inview — секция проявится */
.is-inview.wa-anim-fade-in,
.is-inview.wa-anim-fade-up {
  opacity: 1;
  transform: none;
}

@media (max-width: 900px) {
  .wa-section {
    min-height: 100vh;
  }
  .wa-section__inner {
    max-width: 90%;
  }
}
/* Header (минимум, накинешь своё меню) */
.site-header {
  padding: 18px 0;
}

.site-header .brand {
  font-weight: 800;
  font-size: 28px;
  color: var(--c-blue);
}

.site-main {
  isolation: isolate;
  width: 100%;
}

.wa-zigzag {
